import * as actionTypes from "../actions/types";
import axios from "../../axios";
import axios2 from "axios";

export const setPayerInfo = (body, sendType, URL) => {
  return dispatch => {
    axios({
      method: "post",
      url: "/payers",
      data: { ...body, sendType: sendType },
      headers: {
        Authorization: "Basic DD2020616"
      }
    })
      .then(response => {
        window.location.href = `${URL}`;
      })
      .catch(error => {});
  };
};

export const loadingHandler = bool => {
  return {
    type: actionTypes.LOADING_HANDLER,
    payload: bool
  };
};

export const calculatePrice = () => {
  return {
    type: actionTypes.CALCULATE_PRICE
  };
};
export const getDashboardProjects = () => dispatch => {
  let authToken = localStorage.getItem("access-Token-Edify");
  let bearerToken = { headers: { Authorization: `Bearer ${authToken}` } };

  axios
    .get("/proyecto/search", bearerToken)
    .then(res => {
      dispatch({
        type: "",
        payload: res.data.content
      });
    })
    .catch(err => {
      console.log(err.message);
    });
};

export const showCart = () => {
  return {
    type: actionTypes.SHOW_CART
  };
};

export const showFiveMore = data => {
  return {
    type: "actionTypes.SHOW_FIVE_MORE"
  };
};

export const addItemCart = item => {
  return {
    type: actionTypes.ADD_ITEM_CART,
    payload: { ...item, quantity: 1 }
  };
};

export const addItemQuantity = (itemId, index) => {
  return {
    type: actionTypes.ADD_ITEM_QUANTITY,
    payload: { itemId: itemId, index: index }
  };
};

export const removeItemQuantity = (itemId, index) => {
  console.log(itemId, index);
  return {
    type: actionTypes.REMOVE_ITEM_QUANTITY,
    payload: { itemId: itemId, index: index }
  };
};

export const removeItemCart = (itemId, itemSize, index) => {
  return {
    type: actionTypes.REMOVE_ITEM_CART,
    payload: { id: itemId, size: itemSize, index: index }
  };
};

export const payToMercadoPago = (items, payer, sendType) => {
  const { name, lastName, email, tel, address, addressNumber, zipCode, addressOptional } = payer;
  console.log(items);
  console.log(payer);
  console.log(sendType);
  let shippingConfig = {};
  if (sendType === "RETIRO") {
    shippingConfig = {};
  } else if (sendType === "MERCADOENVIOS") {
    shippingConfig = {
      mode: "me2",
      dimensions: "30x20x30,800",
      receiver_address: {
        zip_code: zipCode,
        street_name: address,
        street_number: Number(addressNumber),
        floor: "",
        apartment: addressOptional
      }
    };
  }

  const sendItems = items.map(item => {
    return {
      ...item,
      unit_price: item.price,
      description: item.name,
      title: item.name
    };
  });
  return dispatch => {
    let URL;
    const body = {
      items: sendItems,
      payer: {
        name: name,
        surname: lastName,
        email: email,
        phone: {
          area_code: "11",
          number: Number(tel)
        },
        address: {
          street_name: address,
          street_number: Number(addressNumber),
          " zip_code": zipCode
        }
      },
      shipments: shippingConfig
    };

    axios({
      method: "post",
      url: "/mercadopago",
      data: body,
      headers: {
        Authorization: "Basic DD2020616"
      }
    })
      .then(response => {
        URL = response.data.msg;
        dispatch(setPayerInfo(body, sendType, URL));
        console.log(response.data.msg);
      })
      .catch(error => {
        console.log(error.response);
      });
  };
};

export const calculateZipCode = zip => {
  return dispatch => {
    axios
      .get(`https://api.mercadolibre.com/users/520240102/shipping_options?zip_code=${zip}&dimensions=30x30x30,800`)
      .then(response => {
        let value = response.data.options.find(option => option.name === "Normal a domicilio").cost;
        console.log(value);
        dispatch({
          type: actionTypes.SET_SEND_VALUE,
          payload: value
        });
      })
      .catch(error => {
        console.log(error.response);
        dispatch({
          type: actionTypes.SET_SEND_VALUE,
          payload: "INVALIDO"
        });
      });
  };
};

export const setChooseSendValue = value => {
  return {
    type: actionTypes.SET_CHOOSE_SEND_VALUE,
    payload: value
  };
};
